@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
.task-input {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    margin-top: 100px;
}

input {
    border: none;
    border-radius: 30px;
    width: 600px;
    height: 40px;
}

form button {
    font-size: 1.4em;
    position: relative;
    right: 40px;
    border: none;
    background-color: white;
    color: red;
    top: 3px;
    cursor: pointer;
}

form button:hover {
    opacity: 30%;
    transition-timing-function:ease-in-out;
    transition: 0.5s;
}
.task {
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 22px;
    background-color: white;
    border-radius: 30px;
    min-width: 500px;
    right: 30px;
}

.task li {
    height: 60px;
    -webkit-flex: 1 1;
            flex: 1 1;
}

li {
    list-style: none;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.task .check {
    font-size: 1.4em;
    position: relative;
    border: none;
    background-color: white;
    color: red;
    top: 3px;
    cursor: pointer;
    left: 5px;
}

.task .cross {
    font-size: 1.4em;
    position: relative;
    border: none;
    background-color: white;
    color: red;
    top: 3px;
    cursor: pointer;
    border-radius: 60px;
    left: 5px;
}

.task button:hover {
    opacity: 30%;
    transition-timing-function:ease-in-out;
    transition: 0.5s;
}

.check, .cross {
    position: absolute;
    font-size: 1px;
    display: block;
    margin: 10px 0;
}

.completed {
    opacity: 50%;
    text-decoration: line-through;
}
.task-list {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    margin-top: 80px;
}



div {
    padding: 10px;
}
img {
    width: 20%;
    position: absolute;
    z-index: -1;
    border-radius: 50px;
    box-shadow: 0px 0px 25px 15px rgba(0,0,0,0.6);
    margin-bottom: 30px;
}
body {
    background-image: linear-gradient(to right, rgba(255,0,0,1), rgba(187, 255, 0, 0));
}

.app {
    font-family: 'Poppins', sans-serif;
    font-weight: bold;
}

h1 {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    margin-top: 35px;
    font-size: 90px;
    font-family: 'Indie Flower', cursive;
}
